.switch{
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
}
.switch input{
    opacity: 0;
    width: 0;
    height: 0;
}
.slider{
    position: absolute;
    cursor: pointer;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    background-color: gray;
    transition: 0.4s;
    border-radius: 34px;
}
.slider:before{
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: azure;
    border-radius: 50%;
    transition: 0.4s;
}
input:checked + .slider{
    background-color: dodgerblue;
}
input:checked + .slider:before{
    transform: translateX(26px);
}